.footer {
  border-top: 1px solid $color-elephant-opacity;
  margin-top: auto;
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  padding-top: 29px;
  padding-bottom: 30px;

  @include vp-1199 {
    padding-top: 42px;
    padding-bottom: 40px;
  }

  @include vp-767 {
    padding: 30px 20px;
  }
}

.footer__nav-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @include vp-1199 {
    justify-content: space-between;
  }

  @include vp-767 {
    align-items: start;
  }

  @include vp-767 {
    flex-direction: column;
  }
}

.footer__nav-wrapper a {
  font-weight: 600;
  font-size: 18px;
  transition: color $trans-default;

  &:disabled {
    opacity: 0.1;
    pointer-events: none;
  }

  @include hover-focus {
    outline: none;
    color: $color-resolution-blue;
  }

  @include active {
    color: $color-navy-blue;
  }

  @include vp-1199 {
    &:last-child {
      display: none;
    }
  }
}

.footer__phone {
  max-width: 142px;
  min-width: 142px;

  @include vp-1199 {
    max-width: 128px;
    min-width: 128px;
  }

  @include vp-767 {
    max-width: 112px;
    min-width: 112px;
  }
}

.footer__nav {
  display: flex;
  flex-wrap: wrap;
  gap: 21px;
  margin-right: 64px;
  padding: 0 70px;

  @include vp-1199 {
    margin-right: 0;
    margin-bottom: 2px;
    gap: 9px;
    padding: 0;
    padding-left: 30px;
  }

  @include vp-767 {
    display: block;
    column-count: 2;
    padding-top: 15px;
    column-gap: 52px;
    margin-left: -6px;
    padding-left: 0;
  }
}

.footer__social-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include vp-767 {
    align-items: start;
  }

  p {
    font-size: 12px;
    text-align: center;
    font-weight: 300;
    margin: 19px 0 0 0;
    line-height: 12px;
  }
}


.footer__logo {
  a {
    @include vp-1199 {
      display: block;
    }
  }

  svg {
    @include vp-1199 {
      width: 118px;
      height: 34px;
    }

    @include vp-767 {
      width: 112px;
    }
  }

  @include vp-1199 {
    height: 34px;
  }
}

.footer__nav a {
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  transition: color, border $trans-default;
  padding: 3px 5px;
  border: 1px solid $color-transparent;

  &:disabled {
    opacity: 0.1;
    pointer-events: none;
  }

  @include hover {
    outline: none;
    color: $color-elephant;
    border-bottom: 1px solid $color-elephant;
  }

  @include focus {
    outline: none;
    color: $color-elephant;
    border: 1px solid $color-elephant;
  }

  @include active {
    color: $color-elephant;
    border: 1px solid $color-transparent;
  }

  @include vp-1199 {
    font-size: 14px;
    line-height: 14px;
    display: block;
  }

  @include vp-767 {
    margin-top: 7px;
    width: min-content;
  }

  &:last-child {
    display: block;

    @include vp-1199 {
      display: block;
    }

    @include vp-767 {
      display: block;
    }
  }

  @include vp-767 {
    &:first-child {
      margin-top: 0;
    }
  }

  @include hover {
    outline: none;
    color: $color-elephant;

    span {
      border-bottom: 1px solid $color-elephant;
    }
  }

  @include focus {
    outline: none;
    color: $color-elephant;

    span {
      border: 1px solid $color-elephant;
    }
  }

  @include active {
    color: $color-elephant;

    span {
      border: 1px solid $color-transparent;
    }
  }
}
